#app {
  display: flex;
  /* background-color:red; */
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-top: 40vh;
}
#inner-div {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  width: 200px;
  border-radius: 100px;
  background-color: purple;
  margin: auto;
}
.change-triangle {
  width: 0;
  height: 0;
  border-bottom: 100px solid white;
  border-right: 100px solid transparent;
}
.change-before {
  height: 100px;
  width: 100px;
  background-color: white;
}

#btn-change-color {
  display: block;
  margin: 50px 0px 0px 0px;
  padding: 10px;
  width: 150px;
}

#btn-change-Shape {
  display: block;
  margin: 10px 0px 0px 0px;
  padding: 10px;
  width: 150px;
}
